import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import CanvasDraw from 'react-canvas-draw'

class App extends React.Component {
    getImg = () => {

    }



    download(canvas) {

        var link = document.createElement('a');
        link.download = 'teste.jpeg';
        link.href = this.saveable.canvasContainer.children[1].toDataURL('image/jpeg')
        link.click();
    }

    render() {
        return (
            <div>
                <div style={{ display: 'flex', marginTop: '50px', justifyContent: 'center', alignItems: 'center' }}>
                    <CanvasDraw
                        style={{ backgroundColor: '#000' }}
                        ref={canvas => (this.saveable = canvas)}
                        hideGrid
                        catenaryColor="white"
                        brushColor="white"
                        brushRadius="10"
                    />
                </div>
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', marginTop: '50px' }}>
                    <button
                        style={{
                            height: 60,
                            width: 180,
                            borderRadius: 10,
                            backgroundColor: 'aqua',
                            fontSize: 25
                        }}
                        onClick={() => {
                            this.download(this.saveable)
                        }}
                    >
                        Salvar
          </button>
                    <div style={{ width: 50 }} />
                    <button
                        style={{
                            height: 60,
                            width: 180,
                            borderRadius: 10,
                            backgroundColor: 'aqua',
                            fontSize: 25
                        }}
                        onClick={() => {
                            this.saveable.clear();
                        }}
                    >
                        Apagar
          </button>
                </div>
            </div >
        );
    }
}



const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);


